import {useEffect, useState} from 'react';
import {Link} from "react-router-dom";
import {useAuth} from "../hook/useAuth";
import {deleteCategory, findCategories} from "../service/category.service";
import {useSnackbar} from 'notistack';


const imageLogo = {width: 62}

export function SearchCategory({idAddNew}) {
    let {isAuthenticated, online, check} = useAuth()

    let {enqueueSnackbar} = useSnackbar()

    // let {setCategory: setItem} = useContext(CategoryContext)
    // console.log(cateCtx)
    //ham hook
    let [search, setSearch] = useState({
        start: 0,
        length: 10,
        keyword: ''
    });

    let [result, setResult] = useState({
        recordsTotal: 0,
        recordsFiltered: 0,
        data: []
    })

    let [success, setSuccess] = useState(false)

    useEffect(() => {
        const timeout = setTimeout(() => {
            //goi lai
            find()
        }, 500)

        return () => {
            clearTimeout(timeout)
        }

    }, [search])

    const find = async () => {
        let {code, result} = await findCategories(search)
        try {
            if (code === 200) {
                setResult(result)
            }
        } catch (error) {
            console.log(error);
        }
    }

    const handleChange = (e) => setSearch({
        ...search,
        [e.target.name]: e.target.value
    })

    const deleteItem = async (id) => {
        let {code} = await deleteCategory(id)
        if (code === 200) {
            find()
            enqueueSnackbar("Delete Success!", {variant: 'success'})
        } else {
            if (code === 408)
                enqueueSnackbar("Loi ket noi mang", {variant: 'error'})
            else if (code === 500) {
                enqueueSnackbar("Loi xay ra", {variant: 'error'})
            } else {
                enqueueSnackbar(`Loi ${code}`, {variant: 'error'})

            }
        }
    }

    const back = () => {
        let start = search.start - parseInt(search.length)
        start = start < 0 ? 0 : start
        setSearch({
            ...search,
            start
        })
        check(1, 2)
    }

    const next = () => {
        let start = (search.start) + parseInt(search.length)
        console.log(start)
        if (start < result.recordsFiltered)
            setSearch({
                ...search,
                start
            })
        check(2, 1)
    }

    return (
        <div>
            <h1>{online.toString()} - {`${isAuthenticated}`}</h1>
            <h2>Search Category</h2>
            <div>
                <label>Keyword</label>
                <input onChange={handleChange} name="keyword"/>

                <select onChange={handleChange} name="length"
                        value={search.length}>
                    <option value={5}>5</option>
                    <option value={10}>10</option>
                    <option value={15}>15</option>
                </select>

                <button type='button' onClick={back}>
                    <i className="fa-solid fa-backward"></i>
                </button>
                <button type='button' onClick={next}>
                    <i className="fa-solid fa-forward"></i>
                </button>
            </div>
            {
                success && <div className='text-success'>
                    <p>Thanh cong!!!!</p>
                </div>
            }

            <table className="table table-striped">
                <thead>
                <tr>
                    <th>Id</th>
                    <th>Name</th>
                    <th>Option</th>
                </tr>
                </thead>
                <tbody>
                {
                    result.data.map(({id, name}) => (
                        <tr key={id}>
                            <td>{id}</td>
                            <td>{name}</td>
                            <td>
                                <button className='btn btn-danger' onClick={() => deleteItem(id)}>
                                    <i className="fa-solid fa-trash-can"></i> Delete
                                </button>
                                <Link className='btn btn-warning' to={`/dashboard/category/${id}/edit`}>
                                    <i className="fa-solid fa-pen-to-square"></i> Edit
                                </Link>
                            </td>
                        </tr>
                    ))
                }
                </tbody>
            </table>
        </div>
    )
}
