import axios from 'axios';
import {useContext, useEffect, useState} from 'react';
import {CategoryContext} from '../context/CategoryContext';
import {useNavigate, useParams} from "react-router-dom";
import {axiosInstance} from "../service/myaxios";

const imageLogo = {width: 62}

export function EditCategory() {
    let {id} = useParams()
    console.log(id)
    //ham hook
    let [category, setCategory] = useState({id, name: ''})

    let navigate = useNavigate()

    useEffect(() => {
        getById()
    }, [id])

    const getById = async () => {
        var config = {
            method: 'get',
            url: `/category/${id}`
        };

        try {
            let response = await axiosInstance(config)

            let result = response.data;// ko co gi
            setCategory(result)
        } catch (error) {
            console.log(error);
        }
    }

    const save = async () => {
        var config = {
            method: 'put',
            url: '/admin/category/update',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": "Bearer " + localStorage.getItem("accessToken")
            },
            data: category
        };

        try {
            let response = await axiosInstance(config)

            let result = response.data;// ko co gi
            console.log(result)
            navigate = ("/dashboard/categories", {replace: true});

        } catch (error) {
            console.log(error);
        }
    }

    const handleChange = (e) => setCategory({
        ...category,
        [e.target.name]: e.target.value
    })

    return (
        <div>
            <h2>Update Category</h2>
            <div>
                <label>Id</label>
                <input onChange={handleChange} name="id" value={category?.id}/>
            </div>
            <div>
                <label>Name</label>
                <input onChange={handleChange} name="name"/>
            </div>
            <div>
                <button type='button' onClick={save}>Update</button>
            </div>
        </div>
    )
}